/* Variables
/* ---------------------------------------------------------- */


@media	only screen and (-webkit-min-device-pixel-ratio: 1.3),
only screen and (-o-min-device-pixel-ratio: 13/10),
only screen and (min-resolution: 120dpi) {
        html {
            font-size: 90% !important;
          }
    }
html {
    font-size: 71% !important;
  }
  @media screen and (prefers-reduced-motion: reduce) {
	html {
		scroll-behavior: auto;
	}
}
  body {
    font-size: 1.83rem !important;
    height: 100%;
    width: 100%;
	scroll-behavior: smooth;
}

html[data-theme="dark"] {
    --color-primary: #5107ed;
    --color-primary-hover: #9a5cfd;
    --color-secondary-text: #e7e7e7;
    --color-base: #e7e7e7;
    --color-bg: #1C2128;
    --color-bg-alt: #22272e;
    --color-link: #e7e7e7;
    --color-bg-white: #1C2128;
    --color-border: #444c56;
    --color-svg: #e7e7e7;
    --border-form-control: #444c56;
    --bg-icon-mail: #539bf5;
    --ghost-accent-color: #9a5cfd;
}

html[data-theme="dark"] h1,
html[data-theme="dark"] h2:not(.gh-pagehead-title),
html[data-theme="dark"] h3,
html[data-theme="dark"] h4:not(.gh-subscribe-title),
html[data-theme="dark"] h5,
html[data-theme="dark"] h6 {
color: var(--color-base)
}

html[data-theme="dark"] .global-button {
background-color: var(--color-secondary);
}

.align-left {
text-align: left;
}

:root {
    /* Colours */
    --color-secondary-text: #939393;
    --color-primary: #ed5107;
    --color-primary-hover: #f67b22;
    --color-secondary: #5107ed;
    --color-secondary-hover: #5107ed;
    --color-success: #07815c;
    --color-danger: #ca203f;
    --color-base: #111;
    --color-gray: #636363;
    --color-border: #f1f1f1;
    --color-bg: #fff;
    --color-bg-alt: #f1f1f1;
    --color-link: #fff;
    --color-shadow: rgba(37,115,247,.05);
    --color-white:#fff;

    /* Fonts */
    --font-sans:  'Inter Tight', 'Lato', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans";
    --font-serif: 'Lora', Georgia, Times, serif;
    --font-mono: Menlo, Courier, monospace;
    --font-light: 300;
    --font-normal: 400;
    --font-bold: 600;
    --font-heavy: 800;

    /* Breakpoints */
    --xlarge: 1680px;
    --large: 1280px;
    --medium: 992px;
    --small: 768px;
    --xsmall: 480px;

    /* Sizes */
    --height: 4rem;
    --margin: 2rem;
    --radius: 0.5rem;
    
    /* intro */
    /* gaps */
    --gap: 1em;
    --gap-rem: 1rem;
    --gap-xs: calc(var(--gap)*0.25);
    --gap-sm: calc(var(--gap)*0.5);
    --gap-lg: calc(var(--gap)*2);
    --gap-xl: calc(var(--gap)*4);

    /* radius */
    --global-radius: 0.25rem;

    /* Global margin */
   --margin-wrap-left-right: 5vw;

   /* Border-radius */
   --border-radius: 0.5rem;

   /* Grid gap */
   --grid-gap: 1.33rem;

   /* Global wrapper */
   --max-width-global-wrap: 1600px;

}

@media screen and (max-width: 767px) {
    :root {
        --gap: 1.11rem;
    }
}

.shadow {
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}
.shadow-newsletter {
    --tw-shadow: 0 20px 25px -5pxrgba(0,0,0,.33),0 8px 10px -6pxrgba(0,0,0,.33);
    --tw-shadow-colored: 0 20px 25px -5pxvar(--tw-shadow-color),0 8px 10px -6px var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow,0 0 #0000),var(--tw-ring-shadow,0 0 #0000),var(--tw-shadow);
}